import React from "react";
import './ExpenseDate.css'
const ExpenseDate = (props) => {

	let date = props.date;
	date = new Date(date);
	const options = { month: 'short' };
	const monthName = date.toLocaleDateString('en-US', options);
	const weekday = new Array(7);
	weekday[0] = "";
	weekday[1] = "";
	weekday[2] = "";
	weekday[3] = "";
	weekday[4] = "";
	weekday[5] = "";
	weekday[6] = "";

	return (
		<div className="expense-date">
			<div className="expense-date__year">{`${date.getFullYear()}`}</div>
			<div className="expense-date__month">{`${date.getMonth() + 1}`}</div>
			<div className="expense-date__date">{`${date.getDate()}`}</div>
			<div className="expense-date__day">{`${weekday[date.getDay()]}`}</div>
		</div>
	)
}
export default ExpenseDate;